// app/notes/@sidebar/default.tsx
"use client";
import Link from "next/link";
import { NoteCreate } from "@/types/note";


const tags: NoteCreate["tag"][] = ["Todo", "Work", "Personal", "Meeting", "Shopping"];

export default function SidebarNotes() {
  return (
    <ul>
      <li><Link href="/notes/filter/all">All notes</Link></li>
      {tags.map((tag) => (
        <li key={tag}>
          <Link href={`/notes/filter/${tag}`}>{tag}</Link>
        </li>
      ))}
    </ul>
  );
}
